import React from "react";
import { useQuery } from "react-query3";
import { CustomColumns, Framework } from "../../types";
import { Button, PageHeader, Space } from "antd";
import { LinkOutlined } from "@ant-design/icons";

const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));
const Table = React.lazy(() => import("components/ui/Table"));

function DashboardFrameworks() {
  const { data: farmeworks, isLoading } = useQuery<Framework[]>(
    "/dashboardframework"
  );

  const columns: CustomColumns<Framework>[] = [
    {
      key: "name",
      title: "Name",
      dataIndex: "name",
    },
    {
      key: "version",
      title: "Version",
      dataIndex: "version",
      align: "center",
      width: "min-content",
    },
    {
      key: "path",
      title: "Path",
      dataIndex: "path",
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      render: (text, record) => {
        return (
          <Space>
            {/* <Modal>
              <EditModal record={record} />
            </Modal> */}
            <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
          </Space>
        );
      },
    },
  ];

  return (
    <PageHeader title="Dashboard Frameworks" subTitle="Base component libraries used for building dashboards." extra={[
      <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/userinterfaces/dashboards/dashboard-frameworks" target="_blank">Documentation</Button>
    ]}>
      <Table
        title="Frameworks"
        // actions={[
        //   <Modal>
        //     <FrameworkModal />
        //   </Modal>,
        // ]}
        columns={columns}
        loading={isLoading}
        data={farmeworks}
      />
    </PageHeader>

  );
}

export default DashboardFrameworks;
